import request from '../BaseAxios'
import Qs from 'qs'
import store from '@/store'


export default {
  //
  dropGroup(groupKeyword) {
    const sid = store.getters.sid
    const data = Qs.stringify({
      C: 'AVEVA.CDMS.WebApi.GroupController',
      A: 'DropGroup',
      sid,
      groupKeyword
    })
    return request({
      url: `WebApi/Post/?${data}`,
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },
  //
  dropUser(groupKeyword, userKeyword) {
    const sid = store.getters.sid
    const data = Qs.stringify({
      C: 'AVEVA.CDMS.WebApi.GroupController',
      A: 'DropUser',
      sid,
      groupKeyword,
      userKeyword
    })
    return request({
      url: `WebApi/Post/?${data}`,
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },
  //
  addUser(groupKeyword, userKeyword) {
    const sid = store.getters.sid
    const data = Qs.stringify({
      C: 'AVEVA.CDMS.WebApi.GroupController',
      A: 'AddUser',
      sid,
      groupKeyword,
      userKeyword
    })
    return request({
      url: `WebApi/Post/?${data}`,
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },
  //Group 
  newGroup(groupCode, groupDesc, parentGroup) {
    const sid = store.getters.sid
    const data = Qs.stringify({
      C: 'AVEVA.CDMS.WebApi.GroupController',
      A: 'NewGroup',
      sid,
      groupCode,
      groupDesc,
      parentGroup
    })
    return request({
      url: `WebApi/Post/?${data}`,
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },
}
