import request from '@/api/BaseAxios'
import Qs from 'qs'
import store from '@/store'

export default {
    //  
    getProfessionList() {
        const sid = store.getters.sid
        const data = Qs.stringify({
            C: 'AVEVA.CDMS.HXPC_Plugins.Profession',
            A: 'GetProfessionList',
            sid,
        })
        return request({
            url: `WebApi/post/?${data}`,
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            }
        })
    },
    //  
    setProfession(prjoectKeyword, designPhase, professionList, startAtWfBtn) {
        const sid = store.getters.sid
        const data = Qs.stringify({
            C: 'AVEVA.CDMS.HXPC_Plugins.Profession',
            A: 'SetProfession',
            sid,
            prjoectKeyword,
            designPhase,
            professionList,
            startAtWfBtn
        })
        return request({
            url: `WebApi/post`,
            method: 'post',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: data
        })
    },
    // () 
    getDesignPhaseProfession(ProjectKeyword, DesignPhaseCode) {
        const sid = store.getters.sid
        const data = Qs.stringify({
            C: 'AVEVA.CDMS.HXPC_Plugins.Profession',
            A: 'GetDesignPhaseProfession',
            sid,
            ProjectKeyword,
            DesignPhaseCode
        })
        return request({
            url: `WebApi/post/?${data}`,
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            }
        })
    },
    // ProjectKeyword() 
    getCreatedProfession(ProjectKeyword) {
        const sid = store.getters.sid
        const data = Qs.stringify({
            C: 'AVEVA.CDMS.HXPC_Plugins.Profession',
            A: 'GetCreatedProfession',
            sid,
            ProjectKeyword,
        })
        return request({
            url: `WebApi/post/?${data}`,
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            }
        })
    },
}