<template>
  <div class="PMmainContainer">
    <!-- , -->
    <el-row class="mainTitle PMmainTitle">
      <!-- <el-col :span="24">
        <h1><i class="el-icon-s-claim" style="color: #FFBC40;"></i> </h1>
      </el-col> -->
      <el-col :span="24">
        <div class="PMmainMultifunctional">
          <el-button size="small" type="primary" icon="el-icon-search" @click="MessagelistHandle('')"></el-button>
          <el-button size="small" type="primary" icon="el-icon-delete" @click="MessagelistHandle('')"></el-button>
          <el-button size="small" type="primary" icon="el-icon-edit" @click="MessagelistHandle('')"></el-button>
          <el-button size="small" type="primary" icon="el-icon-document-add" @click="MessagelistHandle('')"></el-button>
          <el-button size="small" type="primary" icon="el-icon-s-promotion" @click="MessagelistHandle('')"></el-button>
          <el-button size="small" type="primary" icon="el-icon-collection-tag" @click="MessagelistHandle('')" :disabled="isRead"></el-button>
        </div>
      </el-col>
    </el-row>
    <!---->
    <el-table
      ref="messageList"
      :data="messageList"
      class="mainTable PMmainTable"
      height="700"
      highlight-current-row
      @row-click="handleCurrentChange"
      @selection-change="handleSelectionChange"
      :default-sort="{prop: 'date', order: 'descending'}">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column prop="Sender" label="" max-width="20%" min-width="130" sortable show-overflow-tooltip />
      <el-table-column prop="Title" label="" sortable max-width="20%" min-width="250" show-overflow-tooltip />
      <el-table-column prop="SendDatetime" label="" sortable max-width="20%" min-width="200" />
      <el-table-column prop="Workflow_DESC" label="" sortable max-width="20%" min-width="200" show-overflow-tooltip />
      <el-table-column prop="SignificantType" label="" sortable max-width="20%" min-width="200" />
      <el-table-column prop="DelayDay" label="" sortable max-width="20%" min-width="200" />
    </el-table>
    <!--  -->
    <el-dialog
      :title="dialogTitle"
      class="PMmainDialog"
      :visible.sync="dialogFormVisible"
      width="30%"
      :before-close="handleClose">
      <el-form :model="PMmainForm" label-position="left" :label-width="formLabelWidth">
        <el-form-item label=":" prop="Title">
          <el-input v-model="PMmainForm.Title" :readonly="isEdit"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="Sender">
          <el-input v-model="PMmainForm.Sender" readonly />
        </el-form-item>
        <el-form-item label=":" prop="RecUsers">
          <el-input class="inputStyle" v-model="PMmainForm.RecUsers" readonly />
          <el-button class="buttonStyle" type="primary" @click="permissionListHanlde('RecUsers')"></el-button>
        </el-form-item>
        <el-form-item label=":" prop="CCUsers">
          <el-input class="inputStyle" v-model="PMmainForm.CCUsers" readonly />
          <el-button class="buttonStyle" type="primary" @click="permissionListHanlde('CCUsers')"></el-button>
        </el-form-item>
        <el-form-item class="Attachment" label=":">
          <div class="inputStyle">
            <div class="Attachment-div">
              <span
                class="Attachment-span"
                v-for="(item, index) in PMmainForm.AttachmentData"
                :key="index">
                <el-checkbox v-model="item.checked">
                  <span class="Attachment-icon hideText">{{ item.Attachment }}</span>
                </el-checkbox>
              </span>
            </div>
          </div>
          <el-button class="buttonStyle" type="primary" @click="openPermissionList2"></el-button>
        </el-form-item>
        <el-form-item label=":" prop="Content">
          <el-input type="textarea" v-model="PMmainForm.Content" rows="6" :readonly="isEdit"></el-input>
        </el-form-item>
      </el-form>
      <div class="PMmainDialogBottom">
        <el-button size="small" :disabled="send_btn" @click="sendMessage"></el-button>
        <el-button size="small" :disabled="reply_btn" @click="MessagelistHandle('')"></el-button>
        <el-button size="small" :disabled="new_btn" @click="MessagelistHandle('')"></el-button>
        <el-button size="small" :disabled="forward_btn" @click="MessagelistHandle('')"></el-button>
        <el-button size="small" @click="saveDraft"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogDeleteVisible" width="30%">
      <el-row>
        <el-col :span="8">
          <i class="el-icon-warning iconStyle"></i>
        </el-col>
        <el-col :span="16">
          <p>:</p>
          <p>: {{ MessageDeleteTitle }}</p>
        </el-col>
      </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancelHandle"></el-button>
        <el-button type="danger" size="small" @click="deleteMessage"></el-button>
      </span>
    </el-dialog>
    <!--  - / -->
    <PermissionList :dialogObj="PermissionData" @reData="rePData" @reUser="reUser(arguments)" />
    <!--  -  -->
    <SelectAttachment :dialogObj="PermissionData2" @reData="reData2" @Attached="Attached" />
    <!--  -->
    <pagination :pagination="pagination" @changepage="pageNum2" />
  </div>
</template>

<script>
import PermissionList from "../Dialog/PermissionList-PM2"
import SelectAttachment from "../Dialog/SelectAttachment"
import UserApi from '../../api/services/message'
import { mapGetters } from 'vuex'
let username = localStorage.getItem('username') || sessionStorage.getItem('username')
let userdesc = localStorage.getItem('userdesc') || sessionStorage.getItem('userdesc')

export default {
  data() {
    return {
      isEdit: true,
      // 
      multipleSelection: [],
      // 
      dialogFormVisible: false,
      //  
      dialogTitle: "",
      PMmainForm: {
        Sender: '', // 
        RecUsers: '', // 
        RecUserList: '', // 
        Title: '', // 
        Content: '', // 
        CCUsers: '', // 
        CCUserList: '', // 
        AttachmentData: [
          // {
          //   Attachment: ""
          // }
        ]
      },
      formLabelWidth: "120px",
      //  
      send_btn: true,
      reply_btn: false,
      new_btn: false,
      forward_btn: false,
      // 
      dialogDeleteVisible: false,
      MessageDeleteTitle: '',
      //  - /
      PermissionData: {
        switch: false
      },
      //  - 
      PermissionData2: {
        switch: false
      },
      isRead: false
    }
  },
  filters: {
    asd (value) {
      if(value.length > 20) {
        return value.slice(0,19) + '...'
      } else {
        return value
      }
    }
  },
  components: {
    PermissionList,
    SelectAttachment
  },
  computed: {
    ...mapGetters('PerMessage', ['messageList', 'pagination', 'MessageType', 'messageListId'])
  },
  watch: {
    // 
    messageListId: {
      handler(newValue) {
        if (newValue === '_1') {
          this.isRead = false
        } else {
          this.isRead = true
        }
      }
    }
  },
  methods: {
    // Message
    handleCurrentChange(val) {
      let vm = this
      if (val !== null){
        vm.$refs.messageList.clearSelection()
        vm.$refs.messageList.toggleRowSelection(val)
        vm.MessageDeleteTitle = val.Title
        vm.$store.dispatch('PerMessage/getMessage', val.Keyword)
      }
    },
    // Message
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    //  Message 
    MessagelistHandle(e) {
      if (e !== '') {
        let arr = this.multipleSelection
        if (arr.length === 0) {
          this.$message({ message: '', type: 'error' })
          return
        } else {
          if (e === '') {
            this.dialogDeleteVisible = true
            return
          } else if (e === '') {
            this.SetMessageRead(e)
            return
          }
        }
      }
      this.dialogTitle = e
      this.dialogFormVisible = true
      switch (e) {
        case '':
          this.getMessage(e)
          if (!this.isRead) {
            this.SetMessageRead(e)
          }
          if (this.messageListId === '_4') {
            this.send_btn = false
            this.reply_btn = true
          }
        break;
        case '':
          this.send_btn = false
          this.reply_btn = true
          this.getMessage(e)
          // this.new_btn = false
          // this.forward_btn = false
        break;
        case "":
          this.isEdit = false
          this.send_btn = false
          this.reply_btn = true
          this.forward_btn = true
          this.PMmainForm.Sender = `${username}__${userdesc}`
          this.PMmainForm.RecUsers = ''
          this.PMmainForm.Title = ''
          this.PMmainForm.Content = ''
          this.PMmainForm.CCUsers = ''
          this.PMmainForm.AttachmentData = []
        break;
        case "":
          this.send_btn = false
          this.reply_btn = true
          this.forward_btn = true
          this.getMessage(e)
        break;
      }
    },
    // 
    async saveDraft () {
      let { multipleSelection } = this,
          arr = [],
          MessageKeyword = '',
          RecUserlist = this.PMmainForm.RecUserList, 
          CCUserlist = this.PMmainForm.CCUserList,
          Title = this.PMmainForm.Title,
          Content = this.PMmainForm.Content,
          Attalist = '',
          arr2 = []
      if (multipleSelection.length !== 0) {
        arr = multipleSelection.map(item => item.Keyword)
        MessageKeyword = arr[0]
      }
      this.PMmainForm.AttachmentData.forEach(item => {
        if (item.checked) {
          let Keyword = item.AttaKeyword || item.Keyword
          arr2.push(Keyword)
        }
      })
      Attalist = arr2.join(',')
      const res = await UserApi.saveDraftMessage(MessageKeyword, RecUserlist, CCUserlist, Title, Content, Attalist)
      if (res.success) {
        this.$message({ message: '', type: 'success' })
        this.handleClose()
      } else {
        this.$message({ message: res.msg, type: 'error' })
      }
    },
    //  Message
    async deleteMessage () {
      let { multipleSelection } = this
      let arr = multipleSelection.map(item => item.Keyword)
      for(let i in arr) {
        const res = await UserApi.deleteMessage(arr[i])
        if (res.success) {
          this.$message({ message: '', type: 'success' })
        } else {
          this.$message({ message: '', type: 'error' })
          return
        }
      }
      let MessageType = this.MessageType, page = 1
      this.$store.dispatch('PerMessage/getMessageList', { MessageType, page })
      this.cancelHandle()
    },
    //  Message
    async sendMessage () {
      let RecUserlist = this.PMmainForm.RecUserList, 
          CCUserlist = this.PMmainForm.CCUserList,
          Title = this.PMmainForm.Title,
          Content = this.PMmainForm.Content,
          Attalist = '',
          arr = []
      this.PMmainForm.AttachmentData.forEach(item => {
        if (item.checked) {
          let Keyword = item.AttaKeyword || item.Keyword
          arr.push(Keyword)
        }
      })
      Attalist = arr.join(',')
      const res = await UserApi.sendMessage(RecUserlist, CCUserlist, Title, Content, Attalist)
      if(res.success) {
        let MessageType = this.MessageType, page = 1
        this.$store.dispatch('PerMessage/getMessageList', { MessageType, page })
        this.handleClose()
      } else {
        this.$message({ message: res.msg, type: 'error' })
      }
    },
    //  Message
    async SetMessageRead (e) {
      let { multipleSelection } = this
      let arr = multipleSelection.map(item => item.Keyword)
      for(let i in arr) {
        const res = await UserApi.SetMessageRead(arr[i])
        if (res.success) {
          if (e === '') {
            this.$message({ message: e, type: 'success' })
          }
        } else {
          this.$message({ message: res.msg, type: 'error' })
          return
        }
      }
      if (e === '') {
        let MessageType = this.MessageType, page = 1
        this.$store.dispatch('PerMessage/getMessageList', { MessageType, page })
      } 
    },
    //  Message
    async getMessage (e) {
      let { multipleSelection } = this
      let arr = multipleSelection.map(item => item.Keyword)
      const res2 = await UserApi.getMessage(arr[0])
      if (e === '') {
        this.PMmainForm.Title = res2.data[0].Title
        this.PMmainForm.Sender = res2.data[0].Sender
        this.PMmainForm.RecUsers = res2.data[0].Sender
         this.PMmainForm.RecUserList = res2.data[0].SenderKeyword
        this.PMmainForm.CCUsers = res2.data[0].CCUsers
        this.PMmainForm.Content = res2.data[0].Content
        let arr = [...res2.data]
        arr.shift()
        this.PMmainForm.AttachmentData = []
        arr.forEach(item => {
          this.PMmainForm.AttachmentData.push({
            Attachment: item.Attachment,
            AttaKeyword: item.AttaKeyword,
            AttaType: item.AttaType,
            FileSize: item.FileSize,
            checked: true
          })
        })
      } else if (e === '') {
        this.isEdit = false
        // Sender 
        // RecUsers ,
        this.PMmainForm.Sender = `${username}__${userdesc}`
        this.PMmainForm.RecUsers = res2.data[0].Sender
        this.PMmainForm.RecUserList = res2.data[0].SenderKeyword
        this.PMmainForm.Title = `${e}:${res2.data[0].Title}`
        let arr = [...res2.data]
        arr.shift()
        this.PMmainForm.AttachmentData = []
        arr.forEach(item => {
          this.PMmainForm.AttachmentData.push({
            Attachment: item.Attachment,
            AttaKeyword: item.AttaKeyword,
            AttaType: item.AttaType,
            FileSize: item.FileSize,
            checked: true
          })
        })
        this.PMmainForm.Content = 
        `-----------------------
:${res2.data[0].Sender}
:${res2.data[0].SendDate}
:${res2.data[0].RecUsers}
: ${res2.data[0].CCUsers}
:${res2.data[0].Title}

${res2.data[0].Content}`

      } else if (e === '') {
        this.isEdit = false
        // Sender 
        // RecUsers 
        this.PMmainForm.Sender = `${username}__${userdesc}`
        this.PMmainForm.RecUsers = ''
        this.PMmainForm.RecUserList = ''
        this.PMmainForm.Title = `${e}:${res2.data[0].Title}`
        let arr = [...res2.data]
        arr.shift()
        this.PMmainForm.AttachmentData = []
        arr.forEach(item => {
          this.PMmainForm.AttachmentData.push({
            Attachment: item.Attachment,
            AttaKeyword: item.AttaKeyword,
            AttaType: item.AttaType,
            FileSize: item.FileSize,
            checked: true
          })
        })
        this.PMmainForm.Content = 
        `-----------------------
:${res2.data[0].Sender}
:${res2.data[0].SendDate}
:${res2.data[0].RecUsers}
: ${res2.data[0].CCUsers}
:${res2.data[0].Title}

${res2.data[0].Content}`
      }
      this.dialogFormVisible = true
    },
    //  - 
    permissionListHanlde(e) {
      let arr = []
      if (e === 'CCUsers') {
        let {CCUsers} = this.PMmainForm,
            {CCUserList} = this.PMmainForm
        if (CCUsers !== '') this.mergeList(e, CCUsers, CCUserList)
        else this.openPermissionList(e, arr)
      } else if (e === 'RecUsers') {
        let {RecUsers} = this.PMmainForm,
            {RecUserList} = this.PMmainForm
        if (RecUsers !== '') this.mergeList(e, RecUsers, RecUserList)
        else this.openPermissionList(e, arr)
      }
    },
    //   
    mergeList (status, Users, UserList) {
      // ;
      // ,
      let editArr = []
      let isUsers = Users.split(';')
      let isUserList = UserList.split(',')
      for(let i = 0; i < isUsers.length; i++) {
        editArr.push({
          text: isUsers[i],
          id: isUserList[i]
        })
      }
      this.openPermissionList(status, editArr)
    },
    //  - 
    openPermissionList (status, Arr) {
      let vm = this
      vm.PermissionData.buttonTitle = vm.buttonTitle
      vm.PermissionData.editUserData = Arr
      vm.PermissionData.status = status
      vm.PermissionData.switch = true
    },
    //  - 
    rePData(e) {
      this.PermissionData.switch = e
      this.PermissionData.editUserData = undefined
    },
    //  - 
    reUser (e) {
      let vm = this
      if (e[0] === 'CCUsers') {
        vm.PMmainForm.CCUsers = ''
        vm.PMmainForm.CCUserList = ''
        vm.PMmainForm.CCUsers = e[1]
        vm.PMmainForm.CCUserList = e[2]
      } else if (e[0] === 'RecUsers') {
        vm.PMmainForm.RecUsers = ''
        vm.PMmainForm.RecUserList = ''
        vm.PMmainForm.RecUsers = e[1]
        vm.PMmainForm.RecUserList = e[2]
      }
    },
    //  - 
    openPermissionList2() {
      this.PermissionData2.switch = true
    },
    //  - 
    reData2(e) {
      this.PermissionData2.switch = e
    },
    //  - 
    Attached(e) {
      e.forEach(item => {
        this.PMmainForm.AttachmentData.push({
          ...item,
          Attachment: item.Title,
          checked: true
        })
      })
    },
    //  - 
    cancelHandle () {
      this.dialogDeleteVisible = false
    },
    // 
    handleClose() {
      this.isEdit = true
      this.PMmainForm = {
        Sender: '',
        RecUsers: '',
        RecUserList: '',
        Title: '',
        Content: '',
        CCUsers: '',
        CCUserList: '',
        AttachmentData: []
      },
      this.send_btn = true
      this.reply_btn = false
      this.new_btn = false
      this.forward_btn = false
      this.dialogFormVisible = false
    },
    // 
    pageNum2(e) {
      let page = e
      let { MessageType } = this
      this.$store.dispatch('PerMessage/getMessageList', { MessageType, page })
    },
  }
}
</script>
