import request from '../BaseAxios'
import Qs from 'qs'
import store from '@/store'


export default {
  /// 
  /// ProjectList:Project
  /// DocList:Doc
  /// Position:TVProject = 0,LVProject = 1,LVDoc = 2,LVMultiSelected = 3
  /// JObject,success:,total:,msg:,data(JArray):
  /// successtrue,msg
  /// dataJObjectJObject
  /// "Id"Id,"Name":"State""Position"
  getMenuList(ProjectList, Position, TvPosition) {
    const sid = store.getters.sid
    const data = Qs.stringify({
      C: 'AVEVA.CDMS.WebApi.DBSourceController',
      A: 'GetMenuList',
      sid,
      ProjectList,
      Position,
      TvPosition
    })
    return request({
      url: `WebApi/Post/?${data}`,
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      }
    })
  }
}
