import request from '../BaseAxios'
import Qs from 'qs'
import store from '@/store'


export default {
  //
  beforeUploadFile(ObjectKeyword, ServerFileName, CreateDate, ModifyDate, fileSize, MD5) {
    const sid = store.getters.sid
    const data = Qs.stringify({
      C: 'AVEVA.CDMS.WebApi.FileController',
      A: 'BeforeUploadFile',
      sid,
      ObjectKeyword,
      ServerFileName,
      CreateDate,
      ModifyDate,
      fileSize,
      MD5
    })
    return request({
      url: `WebApi/Post/?${data}`,
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },
  //
  //ServerFullFileName:beforeUploadFile
  //Files:
  uploadFile(data, config = {}) {
    return request({
      url: `WebApi/Post`,
      method: 'post',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
        // 'Content-Type': 'application/json'
      },
      data,
      ...config
    })
  },
  //
  //ServerFullFileName:beforeUploadFile
  afterUploadFile(DocKeyword, ServerFullFileName) {
    const sid = store.getters.sid
    const data = Qs.stringify({
      C: 'AVEVA.CDMS.WebApi.FileController',
      A: 'AfterUploadFile',
      sid,
      DocKeyword,
      ServerFullFileName
    })
    return request({
      url: `WebApi/Post/?${data}`,
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      }
    })
  }
}
