<template>
  <div class="creatFile">
    <el-form :model="fileForm" ref="fileForm">
      <el-form-item label="" prop="detail">
        <el-col :span="6">
          <el-input v-model="fileForm.num" readonly />
          <div class="fileLine"></div>
        </el-col>
        <el-col :span="1" class="fileLine"></el-col>
        <el-col :span="6">
          <el-select v-model="fileForm.unit">
            <el-option v-for="item in options" :key="item" :label="item" :value="item"></el-option>
          </el-select>
          <div class="fileLine"></div>
        </el-col>
        <el-col :span="2" class="flexR">
          <div></div>
          <div>S</div>
        </el-col>
        <el-col :span="6">
          <el-input v-model="fileForm.serialNum" />
          <div class="fileLine"></div>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="theme">
        <el-input v-model="fileForm.theme" style="width: 87.6%;" />
      </el-form-item>
      <el-row :gutter="40">
        <el-col :span="12">
          <el-form-item label="" prop="isSend">
            <el-select v-model="fileForm.isSend">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="fileType">
            <el-select v-model="fileForm.fileType">
              <el-option label="EM_" value="EM_"></el-option>
              <el-option label="P_" value="P_"></el-option>
              <el-option label="O_" value="O_"></el-option>
              <el-option label="C_" value="C_"></el-option>
              <el-option label="E_(.pdf)" value="E_(.pdf)"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="way">
            <el-select v-model="fileForm.way">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="OA" value="OA"></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="version">
            <el-select v-model="fileForm.version">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="isSecrecy">
            <el-select v-model="fileForm.isSecrecy">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" class="date" prop="date">
            <el-date-picker type="date" placeholder="" v-model="fileForm.date"></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="purpose">
            <el-select v-model="fileForm.purpose">
              <el-option label="1_" value="1_"></el-option>
              <el-option label="2_" value="2_"></el-option>
              <el-option label="3_" value="3_"></el-option>
              <el-option label="4_" value="4_"></el-option>
              <el-option label="5_" value="5_"></el-option>
              <el-option label="6_" value="6_"></el-option>
              <el-option label="7_" value="7_"></el-option>
              <el-option label="8_" value="8_"></el-option>
              <el-option label="9_" value="9_"></el-option>
              <el-option label="10_" value="10_"></el-option>
              <el-option label="11_" value="11_"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item></el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="" prop="idea">
        <el-input type="textarea" v-model="fileForm.idea" style="width: 87.6%" />
      </el-form-item>
      <el-form-item label="" class="uploadItem" prop="file">
        <div class="uploadFileBox">
          <ul v-if="file">
            <li class="el-upload-list__item is-ready" v-for="(item,index) in file" :key="index">
              <a class="el-upload-list__item-name">
                <img :src="item.src" alt />
                {{item.name}}
              </a>
              <!-- <i class="el-icon-close" @click="handleRemove(index)"></i> -->
            </li>
          </ul>
        </div>
        <div class="fileUpload">
          <UploadFiles :uploadArr="UploadFiles" @reData="reFiles" />
        </div>
      </el-form-item>
    </el-form>
    <span slot="footer" class="footerBtn">
      <el-button @click="cancel"></el-button>
      <el-button type="primary" @click="submit('fileForm')"></el-button>
    </span>

    <el-dialog
      title=""
      :visible.sync="dialogStartWork"
      width="15%"
      :modal-append-to-body="false"
    >
      <div></div>
      <span slot="footer">
        <el-button @click="cancel"></el-button>
        <el-button type="primary" @click="start"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import docAPI from "./api/services/doc";
import proAPI from "../../api/services/project";
import { mapGetters } from "vuex";

export default {
  props: {
    dialogObj: {
      type: Object,
      required: true,
      default: function() {
        return {
          switch: false
        };
      }
    }
  },
  computed: {
    ...mapGetters("doc", ["List"])
  },
  data() {
    return {
      dialogStartWork: false,
      options: [],
      fileForm: {
        num: "",
        unit: "",
        serialNum: "",
        isSend: "",
        isSecrecy: "",
        purpose: "1_",
        date: new Date(),
        idea: "",
        fileType: "EM_",
        way: "",
        version: ""
      },
      file: [],
      programData: {
        switch: false
      },
      ServerFullFileName: "",
      keywordArr: [],
      UploadFiles: [],
      uploadFiles: [], //
      pid: ""
    };
  },
  methods: {
    // 
    start() {
      let docKeyword = this.startKeyword,
        DocList = this.keywordArr.join(", ");
      docAPI.documentStartWorkFlow(docKeyword, DocList).then(res => {
        if (res.success) {
          this.$message({
            type: "success",
            message: ""
          });
          this.cancel();
        } else {
          this.$message({
            type: "error",
            message: res.msg
          });
        }
      });
    },
    dialogClose() {
      this.$emit("reData", false);
    },
    cancel() {
      this.dialogClose();
      this.dialogStartWork = false;
      this.$refs["fileForm"].resetFields();
      this.file = [];
      this.keywordArr = [];
    },
    // 
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let ProjectKeyWord = this.dialogObj.projectId;
          let attrJSON = [
            { name: "RootProject", value: this.fileForm.num },
            { name: "Company", value: this.fileForm.unit },
            { name: "Number", value: this.fileForm.serialNum },
            { name: "Title", value: this.fileForm.theme },
            { name: "If", value: this.fileForm.isSend },
            { name: "ReplyDate", value: this.fileForm.date },
            { name: "Language", value: this.fileForm.version },
            { name: "Mif", value: this.fileForm.isSecrecy },
            { name: "Route", value: this.fileForm.way },
            { name: "File", value: this.fileForm.fileType },
            { name: "SendObj", value: this.fileForm.purpose },
            { name: "Note", value: this.fileForm.idea }
          ];
          let docAttrJson = JSON.stringify(attrJSON);
          docAPI.sendDocument(ProjectKeyWord, docAttrJson).then(res => {
            if (res.success) {
              this.$message({
                message: "",
                type: "success"
              });
              this.getProjectPath(res.data[0].ProjectKeyword);
              this.UploadFiles.ProjectKeyWord = res.data[0].ProjectKeyword
              this.dialogStartWork = true;
              this.startKeyword = res.data[0].DocKeyword;
              this.keywordArr.push(res.data[0].DocList);
            } else {
              this.$message({
                message: res.msg,
                type: "warning"
              });
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleRemove(index) {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          this.fileForm.file.splice(index, 1);
          this.$message({
            type: "success",
            message: ""
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    getSendDocumentDefault() {
      let ProjectKeyword = this.dialogObj.projectId;
      docAPI.getSendDocumentDefault(ProjectKeyword).then(res => {
        if (res.success) {
          this.fileForm.num = res.data[0].RootProjectCode;
          this.fileForm.serialNum = res.data[0].DocNumber;
          this.options = res.data[0].CompanyList;
          this.fileForm.unit = Object.keys(this.options)[0];
        } else {
          this.$message({
            type: "error",
            message: res.msg
          });
        }
      });
    },
    // 
    async getProjectPath(projectKeyword) {
      //  
      await proAPI.saveLastProject(projectKeyword)
      this.$store.commit("project/REFRESH_TREE", true)
    },
    reFiles() {
      let { pid } = this;
      this.$set(this.$refs.DocList.store.states.lazyTreeNodeMap, pid, []);
    }
  },
  watch: {
    dialogObj: {
      handler(newValue) {
        if (newValue.switch) {
          this.UploadFiles.autoUpload = newValue.autoUpload;
          this.getSendDocumentDefault();
        } else {
          return;
        }
      },
      immediate: true,
      deep: true
    },
    List: {
      handler(newValue) {
        console.log(newValue);
        if (newValue) {
          newValue.forEach(item => {
            this.file.push(item);
          });
          console.log(this.file);
        }
      }
    }
  }
};
</script>
<style lang="scss" scope>
.creatFile {
  .flexR {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .el-form-item__label {
    width: 100px;
  }
  .date {
    .el-input__prefix {
      display: none;
    }
    .el-input--prefix .el-input__inner {
      padding-left: 15px;
    }
  }
  .uploadFileBox {
    display: inline-block;
    padding: 5px 15px;
    width: 61%;
    min-height: 96px;
    height: 100%;
    margin-right: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
    }
    li {
      line-height: 20px;
    }
    img {
      width: 5%;
      vertical-align: middle;
    }
  }
  .uploadItem {
    .el-form-item__content {
      display: flex;
      .uploadFileBox {
        flex: 7.5;
      }
      .fileUpload {
        flex: 1;
      }
    }
  }
  .fileUpload {
    display: inline-block;
    vertical-align: top;
  }
  .fileLine {
    text-align: center;
  }
}
</style>