<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogObj.switch"
      width="50%"
      :before-close="dialogClose"
    >
      <el-form ref="form" :rules="rules" :model="form" label-width="120px" class="projectForm">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="projectNum">
              <el-input v-model="form.projectNum"></el-input>
            </el-form-item>
            <el-form-item label="" prop="projectName">
              <el-input v-model="form.projectName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="unitName">
              <el-input v-model="form.unitName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="designStage">
              <el-select v-model="form.designStage" placeholder="">
                <el-option
                  v-for="item in DesignPhase"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="gist">
              <el-input v-model="form.gist"></el-input>
            </el-form-item>
            <el-form-item label="" prop="tenet">
              <el-input v-model="form.tenet"></el-input>
            </el-form-item>
            <el-form-item label="" prop="liaison">
              <el-input v-model="form.liaison"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="form.phone"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="" prop="hr">
              <el-input v-model="form.hr"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="contractNum">
              <el-input v-model="form.contractNum"></el-input>
            </el-form-item>
            <el-form-item label="" prop="contractAmount">
              <el-input v-model="form.contractAmount"></el-input>
            </el-form-item>
            <el-form-item label="" prop="contractType">
              <el-select v-model="form.contractType" placeholder="">
                <el-option
                  v-for="item in ContractModel"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="size">
              <el-input v-model="form.size"></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="rule">
              <el-input v-model="form.rule"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="date">
              <el-date-picker type="date" v-model="form.date" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="startDate">
              <el-date-picker type="date" v-model="form.startDate" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="endDate">
              <el-date-picker type="date" v-model="form.endDate" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="projectStage">
              <el-select v-model="form.projectStage">
                <el-option
                  v-for="item in DevelopmentPhase"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="target">
              <el-select v-model="form.target">
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="nature">
              <el-select v-model="form.nature">
                <el-option
                  v-for="item in EngineeringProperties"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="" class="projectType" prop="type">
              <el-select v-model="form.type" @change="projectTypeChange">
                <el-option
                  v-for="item in ProjectType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
              <el-select v-model="form.type2" class="type2">
                <el-option
                  v-for="item in projectType2Options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="specialReq">
              <el-input type="textarea" :autosize="textareaSize" v-model="form.specialReq"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="summary">
              <el-input type="textarea" :autosize="textareaSize" v-model="form.summary"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="" prop="mainClause">
              <el-input type="textarea" :autosize="textareaSize" v-model="form.mainClause"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"></el-button>
        <el-button type="primary" @click="projectSubmit('form')"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import projectAPI from "./api/services/project";
import proAPI from "../../api/services/project";

export default {
  props: {
    dialogObj: {
      type: Object,
      required: true,
      default: function() {
        return {
          switch: false
        };
      }
    }
  },
  data() {
    return {
      textareaSize: {
        minRows: 4
      },
      DesignPhase: [
        { value: "A__", label: "A__" },
        { value: "B__", label: "B__" },
        { value: "C__", label: "C__" },
        { value: "D__", label: "D__" },
        { value: "E__", label: "E__" },
        { value: "F__", label: "F__" },
        { value: "G__", label: "G__" },
        { value: "H__", label: "H__" },
        { value: "I__", label: "I__" },
        { value: "J__", label: "J__" },
        { value: "K__", label: "K__" }
      ],
      DevelopmentPhase: [
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" }
      ],
      ProjectType: [
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" }
      ],
      projectType2Options: [
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" }
      ],
      EngineeringProperties: [
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" }
      ],
      ContractModel: [
        { value: "BOO", label: "BOO" },
        { value: "BOT", label: "BOT" },
        { value: "C", label: "C" },
        { value: "E", label: "E" },
        { value: "EP", label: "EP" },
        { value: "EPC", label: "EPC" },
        { value: "EPCM", label: "EPCM" },
        { value: "P", label: "P" },
        { value: "PC", label: "PPP" },
        { value: "", label: "" }
      ],
      form: {
        projectNum: "",
        projectName: "",
        unitName: "",
        designStage: "A__",
        gist: "",
        liaison: "",
        phone: "",
        tenet: "",
        hr: "",
        date: new Date(),
        startDate: new Date(),
        endDate: new Date(),
        contractAmount: "",
        contractNum: "",
        contractType: "BOO",
        size: "",
        name: "",
        rule: "",
        type: "",
        type2: "",
        projectStage: "",
        target: "",
        nature: "",
        specialReq: "",
        summary: "",
        mainClause: ""
      },
      rules: {
        projectNum: [
          { required: true, message: "", trigger: "blur" }
        ],
        projectName: [
          { required: true, message: "", trigger: "blur" }
        ],
        unitName: [
          { required: true, message: "", trigger: "blur" }
        ],
        designStage: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    // 
    dialogClose() {
      this.$emit("reData", false);
    },
    projectSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let projectAttrJson = [
            { name: "projectCode", value: this.form.projectNum },
            { name: "projectDesc", value: this.form.projectName },
            { name: "unintName", value: this.form.unitName },
            { name: "designPhase", value: this.form.designStage },
            { name: "HUMAN", value: this.form.hr },
            { name: "PROJECTNOMBER", value: this.form.contractNum },
            { name: "projectAmount", value: this.form.contractAmount },
            { name: "contractModel", value: this.form.contractType },
            { name: "projectSize", value: this.form.size },
            { name: "writeMan", value: this.form.name },
            { name: "TEXTEDITING", value: this.form.rule },
            { name: "projectType", value: this.form.type },
            { name: "projectTypeR", value: this.form.type2 },
            { name: "DESIGNPRINCIPLES", value: this.form.tenet },
            { name: "unitMan", value: this.form.liaison },
            { name: "unitManPhone", value: this.form.phone },
            { name: "releaseDate", value: this.form.date },
            { name: "planStartDate", value: this.form.startDate },
            { name: "planEndDate", value: this.form.endDate },
            { name: "developmentPhase", value: this.form.projectStage },
            { name: "quality", value: this.form.target },
            { name: "CONSTRUCTIONNATURE", value: this.form.nature },
            { name: "projectSummary", value: this.form.summary },
            { name: "Requirement", value: this.form.specialReq },
            { name: "contractTerms", value: this.form.mainClause },
            { name: "designScope", value: this.form.gist }
          ];
          let attrJson = JSON.stringify(projectAttrJson);
          projectAPI.createRootProject(attrJson).then(res => {
            if (res.success) {
              this.$message({
                message: "",
                type: "success"
              });
              this.startNewWorkFlow(res.data[0].projectKeyword)
              this.getProjectPath(res.data[0].projectKeyword)
              this.cancel();
            } else {
              this.$message({
                message: "",
                type: "warning"
              });
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //  selectselect
    projectTypeChange(value) {
      for (let i = 0; i < this.ProjectType.length; i++) {
        if (this.ProjectType[i].value == value) {
          this.form.type2 = this.projectType2Options[i].label;
        }
      }
    },
    cancel() {
      this.$refs["form"].resetFields();
      this.dialogClose();
    },
    // 
    startNewWorkFlow(keyword) {
      let DocList = keyword,
        WfKeyword = "CREATEPROJECT",
        userlist = "";
      projectAPI.startNewWorkFlow(DocList, WfKeyword, userlist).then(res => {
        if (res.success) {
          this.$message({
            type: "success",
            message: ""
          });
        } else {
          this.$message({
            type: "error",
            message: ""
          });
        }
      });
    },
    // 
    async getProjectPath(projectKeyword) {
      //  
      await proAPI.saveLastProject(projectKeyword)
      this.$store.commit("project/REFRESH_TREE", true)
    }
  }
};
</script>
<style lang="scss">
.projectForm {
  .el-select {
    width: 100%;
  }
  .el-form-item__label {
    font-size: 12px;
  }
  .projectType {
    .el-form-item__content {
      display: flex;
      flex-direction: row;
      .el-select:last-child {
        margin-left: 15%;
      }
    }
  }
  .el-form-item__error {
    left: 10px;
  }
  .type2 {
    pointer-events: none;
    .el-input__suffix-inner {
      pointer-events: none;
    }
  }
}
</style>