<template>
  <div>
    <div class="cropper-content">
      <!--  -->
      <div class="cropper">
        <vueCropper
          ref="cropper"
          :img="option.img"
          :outputSize="option.size"
          :outputType="option.outputType"
          :info="true"
          :full="option.full"
          :canMove="option.canMove"
          :canMoveBox="option.canMoveBox"
          :original="option.original"
          :autoCrop="option.autoCrop"
          :autoCropWidth="option.autoCropWidth"
          :autoCropHeight="option.autoCropHeight"
          :fixedBox="option.fixedBox"
          @realTime="realTime"
          :fixed="option.fixed"
          :fixedNumber="fixedNumber"
        ></vueCropper>
      </div>
      <!--  -->
      <div class="show-preview">
        <div :style="previews.div" class="preview">
          <img :src="previews.url" :style="previews.img" />
        </div>
      </div>
    </div>
    <el-row class="dialog-footer">
      <el-col :span="12">
        <el-button type="primary" icon="el-icon-zoom-in" @click="changeScale(1)"></el-button>
        <el-button type="primary" icon="el-icon-zoom-out" @click="changeScale(-1)"></el-button>
        <el-button type="primary" @click="rotateLeft"><i class="el-icon-refresh-left"></i></el-button>
        <el-button type="primary" @click="rotateRight"><i class="el-icon-refresh-right"></i></el-button>
      </el-col>
      <el-col :span="12">
        <el-button type="primary" @click="uploadImg('base64')"></el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { VueCropper } from "vue-cropper";
export default {
  props: ["imgFile", "fixedNumber"],
  data() {
    return {
      previews: {}, // 
      option: {
        img: "", //   ()
        size: 1, //   (:1)
        full: true, //  true  (:false)
        outputType: "png", //   (:jpg)
        canMove: true, //   (:true)
        original: false, //   (:false)
        canMoveBox: true, //   (:true)
        autoCrop: true, //   (:false)
        autoCropWidth: 400, //   (:80%)
        autoCropHeight: 400, //   (:80%)
        fixedBox: false, //    (:false)
        fixed: true, //   (:true)
        fixedNumber: [10, 10] //   (:[1:1])
      },
      downImg: "#"
    };
  },
  components: { 
    VueCropper 
  },
  methods: {
    // 
    changeScale(num) {
      num = num || 1
      this.$refs.cropper.changeScale(num)
    },
    // 
    rotateLeft() {
      this.$refs.cropper.rotateLeft()
    },
    // 
    rotateRight() {
      this.$refs.cropper.rotateRight()
    },
    Update() {
      this.option.img = this.imgFile.url;
    },
    // 
    realTime(data) {
      this.previews = data
    },
    // 
    uploadImg(type) {
      event.preventDefault()
      if (type === "blob") {
        this.$refs.cropper.getCropBlob(data => {
          this.$emit("upload", data)
        })
      } else {
        this.$refs.cropper.getCropData(data => {
          this.$emit("upload", data)
        })
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .dialog-footer {
    display: block;
    margin-top: 20px;
  }
</style>
<style lang="scss">
.cropper-content {
  display: flex;
  display: -webkit-flex;
  justify-content: flex-end;
  -webkit-justify-content: flex-end;
  .cropper {
    width: 350px;
    height: 300px;
  }
  .show-preview {
    flex: 1;
    -webkit-flex: 1;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
    width: 300px;
    height: 300px;
    margin: 0 25px;
    overflow: hidden;
    border: 1px solid #cccccc;
    background: #cccccc;
    margin-left: 40px;
    .preview {
      overflow: hidden;
      border: 1px solid #cccccc;
      background: #cccccc;
    }
  }
}
</style>